import React, { useState } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import contactImg from '../assets/img/contact-img.svg'
export default function Contact() {
    const formInitialDetails = {
        firstName:'',
        lastName:'',
        email:'',
        phone:'',
        message:''
    }
    const [formDetails, setFormDetails] = useState(formInitialDetails)
    const [button, setButton] = useState('Send')
    const [status, setStatus] = useState({})

    const onFormUpdate = (category, value)=>{
        setFormDetails({
            ...formDetails,  // Updates only the current values
            [category] : value
        })
    }

    const handleSubmit = async (e)=>{
        e.preventDefault();
        setButton("Sending .....");
        let response = await fetch("http://localhost:5000/contact/",{
            method: "POST",
            headers:{
                'Content-Type':'Application/json;charset=utf-8',
            },
            body:JSON.stringify(formDetails),
        })
        setButton('Send');
        let result = await response.json();
        setFormDetails(formInitialDetails);
        if(result.code === 200){
            setStatus({success:true, message:"Message sent Successfully"})
        }
        else{
            setStatus({success:false, message:"Message not sent, please try again later"})
        }
    }

  return (
    <section className='contact' id='connect'>
        <Container>
            <Row className='align-items-center'>
                <Col md={6}>
                    <img src={contactImg} alt="" />
                </Col>
                <Col md={6}>
                    <h2>Get in Touch</h2>
                    <form onSubmit={handleSubmit}>
                        <Row>
                            <Col sm={6} className='px-1'>
                                <input type="text" value={formDetails.firstName} placeholder='First Name' onChange={(e) => onFormUpdate('firstName',e.target.value)} />
                            </Col>
                            <Col sm={6} className='px-1'>
                                <input type="text" value={formDetails.lastName} placeholder='Last Name' onChange={(e) => onFormUpdate('lastName',e.target.value)} />
                            </Col>
                            <Col sm={6} className='px-1'>
                                <input type="email" value={formDetails.email} placeholder='Email Address' onChange={(e) => onFormUpdate('email',e.target.value)} />
                            </Col>
                            <Col sm={6} className='px-1'>
                                <input type="tel" value={formDetails.phone} placeholder='Phone Number' onChange={(e) => onFormUpdate('phone',e.target.value)} />
                            </Col>
                            <Col >
                                <textarea row='6' value={formDetails.message} placeholder='Enter Your Message here...' onChange={(e) => onFormUpdate('message',e.target.value)} />
                                <button type='submit'> <span>{button}</span></button>
                            </Col>
                            {
                                status.message && 
                                <Col>
                                    <p className={status.success === false ? 'danger':'success'}> {status.message} </p>
                                </Col>
                            }
                        </Row>
                    </form>
                </Col>
            </Row>
        </Container>
    </section>
  )
}
